html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Raleway", sans-serif;
  font-size: 1.6rem;
}

img {
  width: 30%;
}

/* barra de navegacion */
.navbar {
  padding: 2rem;
  background-color: #ffffff;
}

@media screen and (min-width: 480px) {
  .nav__container {
    width: 120rem;
  }
}
.navegacion {
  position: fixed;
  margin-top: -120px;
  width: 100%;
  z-index: 1;
  box-shadow: 20px 1px 7px 10px rgba(0, 0, 0, 0.12);
  background-color: rgba(100, 100, 100, 0.5);
  backdrop-filter: blur(10px);
}

.collapse.navbar-collapse {
  align-items: center;
  justify-content: space-between;
}

.imag {
  border-radius: 30%;
}

.img {
  border-radius: 20%;
  max-width: 10%;
  position: relative;
  z-index: 2;
}

/* estilos generales */
section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

h1,
h2,
legend {
  font-size: 2.5rem;
  font-family: "Roboto Mono", monospace;
}

h3 {
  font-family: "Roboto Mono", monospace;
}

p {
  font-size: 1.6rem;
  line-height: 1.8;
  font-family: "Roboto Mono", monospace;
}

.seccion-oscura {
  color: white;
  background-color: #1b3945;
}

.seccion-clara {
  color: black;
  background-color: #eeeeee;
}

.seccion-titulo {
  font-size: 2rem;
  pad: 15px 0px;
  font-family: "Roboto Mono", monospace;
}

.seccion-titulo::before,
.seccion-titulo::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background-color: #0a0823;
  margin: 20px 0;
}

@media screen and (max-width: 768px) {
  .seccion-titulo {
    font-size: 1.5rem;
    text-align: center;
  }
}
.seccion-descripcion {
  font-size: 1.2rem;
  color: #584e4e;
}

.seccion-texto {
  font-size: 1.2rem;
}

.t-relative {
  position: relative;
}

.btn-info {
  font-size: 1.5rem;
  margin: 20px;
  transition: all 0.5s;
  color: white;
}

.btn-info:hover {
  font-size: 1.7rem;
  margin: 20px;
  font-weight: 700;
  color: rgb(255, 255, 255);
  border: 2px dotted rgb(177, 177, 177);
}

.texto-negro {
  color: black;
}

.texto-blanco {
  color: white;
}

/* seccion here */
.container-circle {
  background: radial-gradient(rgb(63, 63, 63) 50%, rgb(124, 124, 124) 60%, #ffffff 70%);
  width: 400px;
  height: 400px;
  border-radius: 50%;
  opacity: 0.4;
  position: absolute;
  display: flex;
}

.hero {
  background-color: #f4f4f4;
  min-height: 450px;
  text-align: center;
  margin-top: 120px;
}

.hero-principal {
  max-width: 100%;
  padding: 3rem;
}

.hero-principal h2 {
  font-size: 1.5rem;
  color: #615151;
}

/* seccion sobre mi */
.sobre-mi {
  height: 500px;
  pad: 100px;
}

.sobre-mi .contenedor {
  max-width: 600px;
  text-align: center;
}

.slogan {
  margin-top: 50px;
}

.imag-lg {
  width: 200px;
  height: 200px;
}

/* experiencia */
.experiencia i {
  font-size: 2.5rem;
  color: #2fc7bf;
  background-color: #0a0823;
  padding: 8px 19px;
  border-radius: 50%;
}

.experiencia {
  margin-top: 0px;
  padding: 40px 40px 60px 40px;
}

.experiencia .columna {
  padding: 20px;
  border: 3px solid rgba(128, 128, 128, 0.3019607843);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  transition: all 0.2s ease-in;
}

.experiencia .columna:hover {
  background-color: #1b3945;
  color: white;
}

.experiencia-titulo {
  font-size: 25px;
  font-weight: bold;
  margin: 10px 0;
}

.badges-contenedor {
  display: flex;
  flex-direction: column;
  font-size: 25px;
  font-weight: bold;
  margin: 10px 0;
}

.badge {
  margin: 5px 0;
  padding: 10px 5px;
  display: inline-block;
}

/* Proyectos */
.proyectos-recientes {
  padding: 40px;
}

.proyectos-recientes img {
  height: 100%;
  width: 100%;
  padding: 10px;
  border-radius: 10px;
  display: block;
  transition: all 0.2s ease;
}

.proyecto-contenedor {
  padding: 0;
  margin-bottom: 40px;
}

.overlay {
  transition: all 0.2s ease;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.overlay p {
  font-size: 25px;
  font-weight: bold;
  margin-bottom: 0;
}

.proyecto {
  position: relative;
}

.proyecto:hover {
  box-shadow: 0px 1px 15px 2px rgba(0, 0, 0, 0.1);
}

.proyecto:hover img {
  opacity: 0.2;
}

.proyecto:hover .overlay {
  opacity: 1;
}

.overlay .iconos-contenedor {
  display: flex;
}

.overlay i {
  color: black;
  font-size: 60px;
  margin: 10px;
}

.testimonios {
  padding: 40px 40px 80px 40px;
}

.testimonios .carousel {
  max-width: 800px;
}

.carousel-item {
  height: 500px;
}

.carousel-item .container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.testimonio-imagen {
  height: 150px;
  width: 150px;
  margin: 10px 10px 20px 10px;
}

.testimonio-texto {
  max-width: 70%;
  font-size: 20px;
  text-align: center;
  font-family: "Share Tech Mono", monospace;
}

.testimonio-info {
  margin-bottom: 100px;
}

@media screen and (max-width: 480px) {
  .testimonio-info {
    margin-bottom: 100px;
  }
}
.testimonio-info p {
  margin-bottom: 0;
}

.testimonio-info .cliente {
  font-size: 1.2rem;
}

.testimonio-info .cargo {
  font-size: 0.9rem;
  color: #656060;
}

/* Contactos */
.contacto .container {
  max-width: 1100px;
  min-height: 200px;
  padding: 20px;
}

.contacto .rectangulo {
  margin-top: -40px;
  background-color: rgb(56, 190, 224);
  border-radius: 10px;
  box-shadow: 0px 1px 4px 1px #1b3945;
}

.contacto .row {
  width: 100%;
  display: flex;
  align-items: center;
}

.contacto .descripcion {
  color: white;
  font-size: 1.2rem;
}

.contacto button {
  color: white;
  font-weight: bold;
  background: transparent;
  border: 2px solid white;
  padding: 1.25em 2em;
  margin: 10px;
  border-radius: 100px;
  transition: all 0.2s ease-in-out;
}

.contacto button i {
  color: white;
  font-size: 1.3rem;
  transition: all 0.2s ease-in-out;
}

.contacto button:hover {
  background-color: white;
  color: rgb(13, 110, 253);
  border: dotted;
}

.contacto button:hover i {
  color: rgb(13, 110, 253);
}

/* footer */
.footer {
  min-height: 500px;
}

.footer-logo {
  height: 80px;
  width: 80px;
  margin: 10px;
}

.footer-texto {
  font-size: 1.5rem;
  padding: 20px;
  margin-bottom: 30px;
  font-family: "Quicksand", sans-serif;
}

.iconos-redes-sociales a {
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2px;
  margin: 10px;
  border: 2px solid white;
  border-radius: 50%;
  transition: all 0.2s ease-in;
}

.iconos-redes-sociales I {
  color: white;
  font-size: 1.5rem;
  transition: all 0.2s ease-in;
}

.iconos-redes-sociales a:hover {
  background-color: white;
  border: 2px solid rgb(13, 110, 253);
  border-radius: 10px;
  margin: 10px 20px 10px 20px;
  width: 60px;
}

.iconos-redes-sociales a:hover i {
  color: black;
}

.derechos-de-autor {
  font-size: 15px;
  color: #aeaeae;
  padding: 20px;
}

/* Adaptable responsive */
.container-circle {
  background: radial-gradient(rgb(63, 63, 63) 50%, rgb(124, 124, 124) 60%, #ffffff 70%);
  width: 80px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media screen and (min-width: 319px) {
  .img {
    max-width: 100px;
  }
}
@media screen and (min-width: 424px) {
  .container-circle {
    background: radial-gradient(rgb(63, 63, 63) 50%, rgb(124, 124, 124) 60%, #ffffff 70%);
    width: 150px;
    height: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .img {
    max-width: 150px;
  }
}
@media screen and (min-width: 767px) {
  .container-circle {
    background: radial-gradient(rgb(63, 63, 63) 50%, rgb(124, 124, 124) 60%, #ffffff 70%);
    width: 200px;
    height: 200px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .img {
    max-width: 200px;
  }
}
@media screen and (min-width: 1440px) {
  .container-circle {
    background: radial-gradient(rgb(63, 63, 63) 50%, rgb(124, 124, 124) 60%, #ffffff 70%);
    width: 300px;
    height: 300px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .img {
    max-width: 300px;
  }
}
@media screen and (min-width: 2559px) {
  .container-circle {
    background: radial-gradient(rgb(63, 63, 63) 50%, rgb(124, 124, 124) 60%, #ffffff 70%);
    width: 400px;
    height: 400px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .img {
    max-width: 400px;
  }
}
@media screen and (max-width: 767px) {
  .navbar-brand {
    display: none;
  }
}
@media screen and (min-width: 700px) {
  .hero-inferior-img {
    max-width: 600px;
  }
}
@media screen and (max-width: 400px) {
  .overlay p {
    font-size: 18px;
  }
  .overlay i {
    font-size: 40px;
  }
}
@media screen and (max-width: 800px) {
  .testimonio-imagen {
    height: 120px;
    width: 120px;
  }
  .testimonio-texto {
    font-size: 18px;
  }
}
@media screen and (max-width: 768px) {
  .img {
    z-index: 0;
  }
  .navegacion {
    z-index: 1;
    background-color: rgba(100, 100, 100, 0.5);
    backdrop-filter: blur(10px);
  }
  .imag {
    display: block;
    margin: 0 auto;
  }
}
.navegacion ul li a {
  color: white;
  font-weight: bold;
}

.modal {
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(10px);
  display: flex;
  justify-content: center;
  align-items: center;
  transition: opacity 1s;
}

.contenido h2 {
  color: white;
  text-align: center;
  height: 5rem;
  background-color: #2f9ec7;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contenido {
  background-color: white;
  width: 90%;
  height: 61rem;
  margin: 0 auto;
  box-shadow: 0px 0px 5px 4px rgba(0, 0, 0, 0.1);
  position: relative;
}
.contenido .btn-closeModal {
  position: absolute;
  top: 0;
  right: 0;
  width: 3em;
  height: 5rem;
  background-color: #2fa1c7;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bolder;
  color: white;
  cursor: pointer;
}

.form {
  width: 87%;
  height: 90%;
  margin: 0 auto;
}
.form fieldset {
  height: 100%;
  padding: 4rem 1rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.form fieldset legend,
.form fieldset label {
  text-align: center;
}
.form fieldset input,
.form fieldset textarea {
  border-radius: 0.5rem;
}
.form fieldset input {
  border: 1px solid #e1e1e1;
}
.form fieldset textarea {
  height: 20rem;
}
.form fieldset .btnEnviar {
  background-color: #4ba9c8;
  border: none;
  font-weight: bolder;
  color: white;
}
.form fieldset .btnEnviar:hover {
  background-color: #2fa1c7;
}

.lbl-modal {
  padding: 0.5rem 4rem;
  background-color: #f4f4f4;
  border-radius: 1rem;
  transition: padding 0.6s ease-in-out;
  cursor: pointer;
}

.lbl-modal:hover {
  padding: 0.9rem 6rem;
  border: 2px dotted blue;
}

.bi-envelope-at {
  font-size: 3rem;
  color: cornflowerblue;
}

.visible {
  opacity: 1;
  visibility: visible;
}

.noVisible {
  opacity: 0;
  visibility: hidden;
}

@media screen and (min-width: 768px) {
  .modal {
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.3);
    backdrop-filter: blur(10px);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .modal h2 {
    height: 5rem;
    color: white;
    text-align: center;
    background-color: #db8846;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .contenido {
    background-color: white;
    width: 40rem;
    height: 60rem;
    margin: 0 auto;
  }
  .form fieldset {
    width: 87%;
    height: 100%;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    gap: 1rem;
    padding: 0;
  }
  .form fieldset legend {
    font-size: 2rem;
  }
  .form fieldset textarea {
    height: 20rem;
  }
}
#btn-modal {
  display: none;
}